/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Profile Card */
.profileCard {
  background: white;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  padding: 50px 28px; /* Increased padding for more vertical space */
  width: 100%;
  max-width: 480px;
  margin: 32px auto; /* Increased margin for breathing room */
  animation: fadeIn 0.6s ease-out;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

/* Image Styles */
.imageWrapper {
  position: relative;
  width: 220px; /* Larger image */
  height: 220px;
  border-radius: 50%;
  overflow: hidden;
  border: 6px solid #fff;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  margin-bottom: 32px; /* More spacing below the image */
}

.profileImage {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Text Content */
.profileName {
  font-size: 2.5rem; /* Increased font size */
  font-weight: bold;
  color: #2d3436;
  margin: 24px 0 16px;
}

.profileDesignation {
  font-size: 1.6rem; /* Increased font size */
  color: #636e72;
  margin-bottom: 20px;
}

.profileDetails {
  font-size: 1.3rem; /* Increased font size */
  color: #636e72;
  margin: 20px 0; /* Increased spacing between sections */
  line-height: 1.8;
}

/* Contact Information */
.contactInfo {
  margin-top: 24px;
  font-size: 1.2rem; /* Increased font size */
  line-height: 1.8;
}

.contactInfo a {
  color: #1877f2;
  text-decoration: none;
}

.contactInfo a:hover {
  text-decoration: underline;
}

/* Social Links */
.socialLinks {
  display: flex;
  flex-wrap: wrap;
  gap: 20px; /* Increased gap for better spacing */
  justify-content: center;
  margin-top: 40px; /* Larger spacing above social links */
}

.socialButton {
  width: 160px; /* Larger button width */
  padding: 14px 18px; /* More padding for height */
  font-size: 1.2rem; /* Increased font size */
  font-weight: bold;
  text-align: center;
  border-radius: 12px;
  color: white;
  text-decoration: none;
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.socialButton:hover {
  transform: translateY(-3px);
}

.facebook {
  background: #1877f2;
}

.instagram {
  background: #e4405f;
}

.linkedin {
  background: #0077b5;
}

.portfolio {
  background: #333;
}

/* Responsive Design */
@media (max-width: 768px) {
  .profileCard {
    max-width: 100%;
    padding: 40px 24px; /* Adjusted padding for smaller screens */
  }

  .imageWrapper {
    width: 180px; /* Slightly reduced image size */
    height: 180px;
  }

  .profileName {
    font-size: 2.2rem; /* Adjusted for smaller screens */
  }

  .profileDesignation {
    font-size: 1.4rem;
  }

  .profileDetails {
    font-size: 1.2rem;
  }

  .socialButton {
    width: 140px;
    font-size: 1.1rem;
    padding: 12px 16px;
  }
}

@media (max-width: 480px) {
  .profileCard {
    padding: 32px 20px; /* Reduced padding for extra-small screens */
  }

  .imageWrapper {
    width: 150px;
    height: 150px;
  }

  .profileName {
    font-size: 2rem; /* Adjusted for extra-small screens */
  }

  .profileDesignation {
    font-size: 1.3rem;
  }

  .profileDetails {
    font-size: 1.1rem;
  }

  .socialButton {
    width: 120px;
    font-size: 1rem;
    padding: 10px 12px;
  }
}
